import Vue from 'vue'
import Router from 'vue-router'
const Index = () => import('/page/index.vue')
const Login = () => import('/page/Login/login.vue')
const Register = () => import('/page/Login/register.vue')
const Home = () => import('/page/Home/home.vue')
// 产品列表
const product = () => import('/page/product/product.vue')
const productlist = () => import('/page/product/productlist.vue')
const productdetail = () => import('/page/product/productdetail.vue')
const infolist = () => import('/page/product/infolist.vue')
// 平台服务注册
const platform = () => import('/page/platform/platform.vue')
const stepone = () => import('/page/platform/stepone.vue')
const steptwo = () => import('/page/platform/steptwo.vue')
const stepthree = () => import('/page/platform/stepthree.vue')
const stepfour = () => import('/page/platform/stepfour.vue')
const stepfive = () => import('/page/platform/stepfive.vue')
const stepsix = () => import('/page/platform/stepsix.vue')
// 科研
const scientific = () => import('/page/scientific/scientific.vue')
const frontier = () => import('/page/scientific/frontier.vue')
const union = () => import('/page/scientific/union.vue')
const search = () => import('/page/scientific/search.vue')
const support = () => import('/page/scientific/support.vue')
const about = () => import('/page/scientific/about.vue')
const scientificlist = () => import('/page/scientific/scientificlist.vue')
const scientificdetail = () => import('/page/scientific/scientificdetail.vue')
const standard = () => import('/page/scientific/standard.vue')
const institutionlist = () => import('/page/scientific/institutionlist.vue')
const institutiondetail = () => import('/page/scientific/institutiondetail.vue')
// 个人信息
const mine = () => import('/page/mine/mine.vue')
const personal = () => import('/page/mine/personal.vue')
const task = () => import('/page/mine/task.vue')
const manage = () => import('/page/mine/manage.vue')
const manageal = () => import('/page/mine/manageal.vue')
const distribute = () => import('/page/mine/distribute.vue')
const distributeal = () => import('/page/mine/distributeal.vue')
const cart = () => import('/page/mine/cart.vue')
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      component: Index,
      name: 'index',
      redirect: '/home',
      children: [
        { path: 'home', component: Home },
      ]
    },
    { path: '/login', name: 'login', component: Login },
    { path: '/register', name: 'register', component: Register },
    {
      path: '/product',
      name: 'product',
      component: product,
      redirect: '/product/list',
      children: [
        { path: 'list', name: 'productlist', component: productlist },
        { path: 'productdetail', name: 'productdetail', component: productdetail },
        { path: 'infolist', name: 'infolist', component: infolist },
      ]
    },
    {
      path: '/platform',
      name: 'platform',
      component: platform,
      redirect: '/platform/stepone',
      children: [
        { path: 'stepone', name: 'stepone', component: stepone },
        { path: 'steptwo', name: 'steptwo', component: steptwo },
        { path: 'stepthree', name: 'stepthree', component: stepthree },
        { path: 'stepfour', name: 'stepfour', component: stepfour },
        { path: 'stepfive', name: 'stepfive', component: stepfive },
        { path: 'stepsix', name: 'stepsix', component: stepsix },
      ]
    },
    {
      path: '/scientific',
      name: 'scientific',
      component: scientific,
      redirect: '/scientific/frontier',
      children: [
        { path: 'frontier', name: 'frontier', component: frontier },
        { path: 'union', name: 'union', component: union },
        { path: 'search', name: 'search', component: search },
        { path: 'support', name: 'support', component: support },
        { path: 'about', name: 'about', component: about },
        { path: 'scientificlist', name: 'scientificlist', component: scientificlist },
        { path: 'scientificdetail', name: 'scientificdetail', component: scientificdetail },
        { path: 'standard', name: 'standard', component: standard },
        { path: 'institutionlist', name: 'institutionlist', component: institutionlist },
        { path: 'institutiondetail', name: 'institutiondetail', component: institutiondetail },

      ]
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine,
      redirect: '/mine/personal',
      children: [
        { path: 'personal', name: 'personal', component: personal },
        { path: 'task', name: 'task', component: task },
        { path: 'manage', name: 'manage', component: manage },
        { path: 'manageal', name: 'manageal', component: manageal },
        { path: 'distribute', name: 'distribute', component: distribute },
        { path: 'distributeal', name: 'distributeal', component: distributeal },
        { path: 'cart', name: 'cart', component: cart },


      ]
    },
    { path: '*', redirect: '/home' }
  ]
})



